<template>
		<div class="sameDom swiper-container scroll">
			<div class="swiper-wrapper">
				<div class="swiper-slide sliderscroll">
					<slot name="preview"></slot>
				</div>
			</div>
		</div>
</template>
<script type="text/javascript">
import Swiper from 'swiper'
import 'swiper/dist/css/swiper.min.css'
	export default{
		name:'sameDom',
		data(){
			return{
				swiper:Swiper,
				swiperscroll:null
			}
		},
	    props: {
	        infoCode: String,
	        picinfoCode: {
	            type: String,
	            default: '// TODO'
	        }
	    },
	    mounted(){
			      this.swiperscroll = new this.swiper('.scroll',{
			          slidesOffsetBefore: 15,
			          direction: 'vertical',
			          freeMode: true,
			          slidesPerView: 'auto',
			          mousewheel: {
			            releaseOnEdges: true,
			          }
     				 })
	    },
		components:{

		}
	}
</script>


