<template>
  <div id="app">
      <div id="top">
        <span class="logo">logo:</span><span>position:{{barPosition}} </span>
        <div> Message:</div>
        <div id="nav" class="swiper-container">
            <div class="swiper-wrapper">
                <div v-for="(bar,key) in routerName" class="swiper-slide">
                  <span>
                    <router-link :to="{path:key}">{{bar}}</router-link>
                  </span>
                  </div>
                    <div class="bar" ref = 'bar' :style="x">
                    <div class="color"></div>
                  </div>
            </div>          
        </div>
      </div>
        <div id="Container" class="swiper-container">
            <div class="swiper-wrapper">
              <div v-for="(bar,key) in routerName" class="swiper-slide">
                <router-view :name='key'></router-view>
              </div>
            </div>
        </div>
  </div>
</template>

<script>
import Swiper from 'swiper'
import 'swiper/dist/css/swiper.min.css'
export default {
  name: 'app',
  data(){
    return {
      swiper:Swiper,
      navSwiper:Object,
      SwiperPage:Object,
      barPosition:0,
      routerName:{'Model3D-list':'3D','animate':'Animate','error-list':'APP','brach-list':'微信'},
      routerNum:null
    }
  },
  computed:{
      x(){
        return `transform:translateX(${this.barPosition}px)`;    
      }
  },
  update:{
  },
  watch:{
  },
  created(){
  },
  mounted(){
    this.routerNum=Object.keys(this.routerName) //将已生成对象变成键值对
    this.SwiperInit();
  },
 methods:{
    SwiperInit(){
      let that = this;
      let navSlideWidth,navSum,clientWidth,navWidth,slideProgress,barDom
      that.navSwiper=new this.swiper('#nav',{
            slidesPerView: 3,
            slideToClickedSlide: true,
            freeMode: true,
            on:{
              init(){
                navSlideWidth = this.slides.eq(0).css('width'); 
                barDom = this.$el.find('.bar')
                barDom.css('width', navSlideWidth)
                barDom.transition(300)
                navSum = this.slides[this.slides.length - 1].offsetLeft 
                clientWidth = parseInt(this.$wrapperEl.css('width')) 
                navWidth = 0 
                for (let i = 0; i < this.slides.length; i++) {
                navWidth += parseInt(this.slides.eq(i).css('width'))
               }
              }
            }
        }),
          this.NavMove(that,navSum,clientWidth,navSlideWidth)
            that.navSwiper.$el.on('touchstart', function(e) {
            e.preventDefault() //去掉按压阴影
            })
            // 
            that.navSwiper.on('tap', function(e) {
              let clickIndex,clickSlide
              clickIndex = this.clickedIndex
              clickSlide = this.slides.eq(clickIndex)
              that.SwiperPage.slideTo(clickIndex, 0);
            })
      },
      NavMove(that,navSum,clientWidth,navSlideWidth,navWidth){
         that.SwiperPage = new this.swiper('#Container',{
            watchSlidesProgress: true,
            resistanceRatio:0,//抵抗率
            on:{
              touchMove(){
               that.barPosition = navSum*this.progress;
              },
              transitionStart(){
             let activeIndex = this.activeIndex
             let navSwiper = that.navSwiper
             that.$router.push(that.routerNum[activeIndex])
              that.barPosition    =   navSwiper.slides[activeIndex].offsetLeft
              navSwiper.setTransition(300)
                if (that.barPosition < (clientWidth - parseInt(navSlideWidth)) / 2) {
                  navSwiper.setTranslate(0)
                } else if (that.barPosition > navWidth - (parseInt(navSlideWidth) + clientWidth) / 2) {
                  navSwiper.setTranslate(clientWidth - navWidth)
                } else {
                  navSwiper.setTranslate((clientWidth - parseInt(navSlideWidth)) / 2 - that.barPosition)
                }
              }
            } 
      })
      }
  },
}
</script>

<style lang="css">
html,body,#app,#Container{
  height:100%;
  margin: 0;
  padding:0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
/*nav*/
#top {
  position: fixed;
  top: 0;
  z-index: 5;
  width: 100%;
  background: #fff;
  height: 75px;
  }
#top .logo {
  position: absolute;
  left: 0;
  padding-left: 10px;
  padding-top: 10px;
}
#nav{
  text-align: center;
  border-bottom:1px solid #dedede;
}
#nav a{
  text-decoration: none;
}

#Container{
    height: 100%;
}
#nav .swiper-slide span a {
  margin:0 5px;
  text-align:center;
  display:block;
  line-height:2.5;
  font-size:14px;
  color:#505050;
}
#nav .bar {
  width:50px;
  height:3px;
  position:absolute;
  bottom:0px;
}
#nav .bar .color {
  width:36px;
  margin:0 auto;
  height:3px;
  background: #f85959;
}
/*sameDom*/
  .scroll {
    height: 100%;
      width: 100%;
  }
/*Model3D*/
  .list{
    height:150px;
    padding:0;
    margin-top:10px;
    border-bottom:1px solid #dedede;
  }
  .sliderscroll {
    height:auto;
   margin-top: 75px;

  }
  .list p{text-align:center;margin:1px;font-size:15px;}
  .list div.content{
    
  }
</style>


